<template>
    <div class="vertical-wrapper" style="padding: 0 20%;">
        <div class="horizontal-wrapper">
            <h2>Verbindungseinstellungen</h2>
            <div style="flex: 1 0 0"></div>
            <md-button class="md-raised md-primary" v-on:click="connect">Verbinden</md-button>
        </div>
        <div>
            <md-field>
                <label for="">APN</label>
                <md-input v-model="apn"></md-input>
            </md-field>
        </div>
        <div style="text-align: left;">
            Verbindungsfortschritt
            <md-progress-bar md-mode="determinate" :md-value="connectionProgress"></md-progress-bar>
        </div>
        <div class="vertical-wrapper">
            <div class="vertical-wrapper">
                <div class="horizontal-wrapper">
                    <h4 style="">Verbindungsstatus:</h4>
                    <div></div>
                    <div style="flex: 0 0 20pt" class="vertical-wrapper">
                        <div></div>
                        <svg  height="20" width="20">
                            <circle cx="10" cy="10" r="10" :fill="connectionColor"/>
                        </svg>
                        <div></div>
                    </div>
                </div>
                <div class="vertical-wrapper">

                    <div class="horizontal-wrapper" >
                        <h4 style="text-align: left; flex: 1 0 auto">Verbindungsstärke (RSSI):</h4>
                        <div></div>
                        <div>
                            <h4 style="" >{{ rssi }}</h4>
                        </div>
                         <div style="flex: 0 0 20pt" class="vertical-wrapper">
                            <div></div>
                            <svg  height="20" width="20">
                                <circle cx="10" cy="10" r="10" :fill="rssiColor"/>
                            </svg>
                            <div></div>

                        </div>
                    </div>

                    <div class="horizontal-wrapper" >
                        <h4 style="text-align: left; flex: 1 0 auto">Verbindungsfehlerquote (BER):</h4>
                        <div></div>
                        <div>
                            <h4 style="">{{ ber }}</h4>
                        </div>
                        <div style="flex: 0 0 20pt" class="vertical-wrapper">
                            
                            <div></div>
                                <svg  height="20" width="20">
                                <circle cx="10" cy="10" r="10" :fill="berColor"/>
                            </svg>
                            <div></div>
                        </div>
                    </div>
                </div>

            </div>
            <div id="chart" ref="chart">
                <vue-apexchart v-if="drawChartStart" ref="apexchart" type="line" :height="chartBoxHeight" :options="chartOptions" :series="series"></vue-apexchart>
            </div>

           
        </div>

    </div>
</template>
<script>
import { mapState } from 'vuex';

export default{
    name: 'mobil-settings',
    computed: {
        ...mapState('mobileStore', {
            rssi: state => state.rssi,
            ber: state => state.ber,
            state: state => state.state,
            connection: state => state.connection,

            rssiRecord: state => state.rssiRecord,
            berRecord: state => state.berRecord,
        }),
        apn:{
            get(){
                return this.$store.state.mobileStore.apn
            },
            set(value){
                this.$store.commit('mobileStore/set-apn', value)
            }
        },
        connectionColor(){
            if(this.connection)
                return '#00FF00'
            else
                return 'red'

        },
        rssiColor(){
            if(this.rssi > 25 && this.rssi < 90)
                return '#00FF00'
            else if (this.rssi > 15 && this.rssi < 90)
                return '#FBFB43'
            else if (this.rssi > 10 && this.rssi < 90)
                return  '#F7BA30'
            else if (this.rssi > 5 && this.rssi < 90)
                return '#EC031D'

            return 'red'
        },
        berColor(){
            if (this.ber < 3 && this.ber != 0)
                return '#00FF00'
            else if (this.ber < 5 && this.ber != 0)
                return '#FBFB43'
            else if (this.ber < 6 && this.ber != 0)
                return '#F7BA30'
            return 'red'
        },

        connectionProgress(){
            return (parseInt(this.state) / 5) * 100
        }
       

    },
    methods:{
        async getMobileInfo(){
            console.log('call getMobileInfo');
            await this.$store.dispatch('mobileStore/mobile/get-info')

            this.drawChart()
            console.log('fetch getMobileInfo');

            if(!this.requestActive){
                console.log('break getMobileInfo');
                return
            }
            const vm = this
            setTimeout(() => {
                vm.getMobileInfo()
            }, 1000)
        },
        drawChart(){
            // this.$refs['mobil-chart'].update()
            const vm = this
            const chartOptions = {
                toolbar: { show: false},
                chart: {
                    height: this.chartBoxHeight,
                    type: 'line',
                    zoom: {
                        enabled: false
                    },
                    events:{
                        legendClick(chartContext, seriesIndex){
                            if(seriesIndex == 0)
                                vm.hideMobilRSSI = !vm.hideMobilRSSI
                            if(seriesIndex == 1)
                                vm.hideMobilBER = !vm.hideMobilBER
                        }
                    }
                },
                dataLabels: {
                    enabled: false
                },
                stroke: {
                    curve: 'straight',
                    width: 1
                   
                },
                title: {
                    text: 'Signalstärke',
                    align: 'left'
                },
                grid: {
                    row: {
                        colors: ['#f3f3f3', 'transparent'], // takes an array which will be repeated on columns
                        opacity: 0.5
                    },
                },
                xaxis: {
                    type: 'datetime'
                }
            }
            
            if(!this.drawChartStart){
                this.chartOptions = chartOptions
            }
            
            this.series = [
                        { 
                            name: 'Signal RSSI', 
                            data: !this.hideMobilRSSI? this.rssiRecord.map(e => { return [new Date(e.timestamp), e.value]}) : []
                        },
                        {
                            name: 'Signal BER',
                            data: !this.hideMobilBER? this.berRecord.map(e => { return [new Date(e.timestamp), e.value]}): []

                        }]


            this.$nextTick(() => {
                this.drawChartStart = true
            })
        },

        async connect(){
            this.$store.dispatch('mobileStore/mobile/set-info')
        }
    },
    data(){
        return{
        
            username: '',
            password: '',
            drawChartStart: false,

            requestActive: false,

            chartOptions: {},
            series: [],
            
            chartBoxHeight:300,

            hideMobilRSSI:false,
            hideMobilBER:false,

        
        }
    },
    beforeRouteEnter(to, from, next){
        next(vm => {
            vm.requestActive = true

            vm.getMobileInfo()
            vm.drawChart()
        })
    },
    beforeRouteLeave(to, from, next){
       this.requestActive = false;
       next()
    },
}
</script>