<template>
    <div class="centered-container">
      <md-content class="md-elevation-3">
  
        <div class="title">
          <img src="https://vuematerial.io/assets/logo-color.png">
          <div class="md-title">IotAdapter</div>
          <div class="md-body-1">Verwende die Raspberry Pi SSH Login Daten</div>
        </div>
  
        <div class="form">
          <md-field :class="{ 'md-invalid': loginFailed }">
            <label>Username</label>
            <md-input v-model="login.username" autofocus></md-input>
            <span class="md-error">Invalid Creditals</span>

          </md-field>
  
          <md-field md-has-password :class="{ 'md-invalid': loginFailed }">
            <label>Password</label>
            <md-input v-model="login.password" type="password" v-on:keyup.enter="auth"></md-input>
            <span class="md-error">Invalid Creditals</span>

          </md-field>
        </div>
  
        <div class="actions md-layout md-alignment-center-space-between">
          <div></div>
          <md-button class="md-raised md-primary" @click="auth">Log in</md-button>
        </div>
  
        <div class="loading-overlay" v-if="loading">
          <md-progress-spinner md-mode="indeterminate" :md-stroke="2"></md-progress-spinner>
        </div>
  
      </md-content>
      <div class="background" />
    </div>
  </template>
  
  <script>
  export default {
    name: "App",
    data() {
      return {
        loading: false,
        login: {
          username: "",
          password: ""
        },
        loginFailed: false,
      };
    },
    methods: {
      auth() {
        // your code to login user
        // this is only for example of loading
        this.loading = true;

        const body = this.login
        fetch(`${this.$hostname}/login`, { 
          method: 'POST', 
          headers: {
                    'Content-Type': 'application/json'
              },
          body: JSON.stringify(body)
        }).then(async(e) => {
            if(e.status == 200){
                // set cookies
                const body = await e.json()

                if(body.session_id){
                  this.$store.commit('set-token', body.session_id)
                  this.$router.push({ name: 'home-view' })
                }
            }else{
                this.loginFailed = true
                this.loading = false;

            }
        })
        setTimeout(() => {
          this.loading = false;
        }, 5000);
      }
    }
  };
  </script>

