import VueRouter from 'vue-router'

import home from '../views/home-view.vue'
import loginView from '../views/login-view.vue'




export default new VueRouter({
  mode:'history',
  routes: [
    { path: '/login', name:'login-view', component: loginView },
    
    { 
      path: '/', name: 'home-view', component: home, redirect:{ name:'reads7-view'}, children: [
            { name: 'reads7-view', path: '/home/config-reads7', component: require('../views/config-views/readS7-view.vue').default},
            { name: 'writes7-view', path: '/home/config-writes7', component: require('../views/config-views/writeS7-view.vue').default},

            { name: 'network-settings', path: '/home/network-settings', component: require('../views/config-views/network-settings.vue').default},
            { name: 'mobil-settings', path: '/home/mobil-settings', component: require('../views/config-views/mobil-settings.vue').default}

        ]
    },
    
  
    { path: '*', template: '<p>404</P>' } // Not found
  ]
})