<template>

    <md-app md-waterfall md-mode="fixed" style="height: 100%;">
      <md-app-toolbar class=" md-primary">
        <div class="md-toolbar-row">
          <div class="md-toolbar-section-start">
            <md-button class="md-icon-button" @click="menuVisible = !menuVisible">
              <md-icon :md-src="require('../assets/bars-solid.svg')">menu</md-icon>
            </md-button>

            <span class="md-title">IotAdapter Einstellungen</span>

            <div v-if="s7InvalidInputs" style="margin: 0 10pt; position: relative;">
                <md-icon :md-src="require('../assets/triangle-exclamation-solid.svg')"></md-icon>
                <i class="badge" >{{ s7InvalidInputs }}</i>
            </div>

           
          </div>

          <div class="md-toolbar-section-end">
           

            <md-button class="md-icon-button" @click="saveConfiguration">
              <md-icon :md-src="require('../assets/floppy-disk-solid.svg')"></md-icon>
            </md-button>
            <div></div>
            <!--
            <md-button class="md-icon-button">
              <md-icon>more_vert</md-icon>
            </md-button>
          -->
          </div>
        </div>

      </md-app-toolbar>

      <md-app-drawer :md-active.sync="menuVisible">
        <md-toolbar class="md-transparent" md-elevation="0">Navigation</md-toolbar>

        <md-list>
            <router-link :to="{ name: 'reads7-view' }"  @click.native="menuVisible = false">
                <md-list-item>
                    <md-icon :md-src="require('../assets/network-wired-solid.svg')">Icon</md-icon>
                    <span class="md-list-item-text">S7 PUT&GET (GET)</span>
                </md-list-item>
            </router-link>
            <router-link class="hide" :to="{ name: 'writes7-view' }"  @click.native="menuVisible = false">
                <md-list-item>
                    <md-icon :md-src="require('../assets/network-wired-solid.svg')">Icon</md-icon>
                    <span class="md-list-item-text">S7 PUT&GET (PUT)</span>
                </md-list-item>
            </router-link>

          <md-list-item class="hide">
            <md-icon>send</md-icon>
            <span class="md-list-item-text">AnoPi</span>
          </md-list-item>

          <router-link :to="{ name: 'network-settings' }" @click.native="menuVisible = false">
            <md-list-item >
                <md-icon :md-src="require('../assets/ethernet-solid.svg')" ></md-icon>
                <span class="md-list-item-text">Network Settings</span>
            </md-list-item>
          </router-link>

          <router-link v-if="state != 0" :to="{ name: 'mobil-settings' }"  @click.native="menuVisible = false">
                <md-list-item>
                    <md-icon :md-src="require('../assets/tower-broadcast-solid.svg')">Icon</md-icon>
                    <span class="md-list-item-text">Mobil Data</span>
                </md-list-item>
            </router-link>

        </md-list>
      </md-app-drawer>

        <md-app-content style="padding: 0 ;">
            <router-view></router-view>
            
        </md-app-content>
    </md-app>
</template>

<script>
import { mapState } from 'vuex';

export default{
    name: 'home-view',
    watch:{
        menuVisible(){
            this.$store.dispatch('mobileStore/mobile/get-info')
        }
    },
    computed:{
        s7InvalidInputs(){
            return this.$store.getters.getS7InvalidInputs
        },
        config(){
            return this.$store.getters.getConfig
        },
        token(){
            return this.$store.getters.token
        },
        ...mapState('mobileStore', {
            state: state => state.state,
        })
    },
    methods:{
        async saveConfiguration(){
            if(this.s7InvalidInputs != 0){
                this.$swal.fire({
                    title: 'Fehler', 
                    text: 'In der S7 PUT&GET Konfiguration sind üngültige Eingaben',
                    icon: 'warning'
                })

                return
            }

            this.$store.commit('set-config-loading', true)

            this.$store.commit('set-reset-user-input', true)

            const body = {
                config : this.config
            }
            const res = await fetch(`${this.$hostname}/set-config`, {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json',
                    'Authorization': this.token  // Set the Authorization header

                },
                body: JSON.stringify(body)
            })
            if(res.ok){
                return
            }



            this.$store.dispatch('setConfig')
        }
    },
    beforeRouteEnter(to, from, next){
        next(vm => {
            vm.$store.dispatch('mobileStore/mobile/get-info')
        })
    },
    data(){
        return {
            menuVisible: false,
        }
    }
}
</script>

