import Vue from 'vue';
import Vuex from 'vuex';

import mobileStore from './mobileStore.js';

Vue.use(Vuex);

const store = function createStore(router) {
    const $hostname = Vue.prototype.$hostname;

    return new Vuex.Store({
        modules: {
            // Add modules here
            mobileStore: mobileStore
        },
        state: () => ({
            token: '',
            // Initialize config as an object instead of a string
            config: { domain: '', data: [] },
            s7InvalidInputs: 0,
            resetUserInput: false,

            dataConfigLoading: false
        }),
        mutations: {
            increment(state) {
                state.count++;
            },

            'set-token': function(state, token) {
                state.token = token;
            },
            'set-admin': function(state, admin) {
                state.admin = admin;
            },
            's7-invalid-inputs-reset': function(state) {
                state.s7InvalidInputs = 0;
            },
            's7-invalid-inputs-add': function(state, value) {
                state.s7InvalidInputs += value;
                if (state.s7InvalidInputs < 0) {
                    state.s7InvalidInputs = 0;
                }
            },
            'set-config': function(state, value){
                console.log('set-config')
                state.config = value
            },
            'change-s7-config': function(state, s7ConfigItems) {

                
                // Filter existing config data that are not 's7'
                const otherConfigItems = state.config.data.filter(e => e.type !== 's7');
                // Update state.config.data while ensuring reactivity
                state.config.data = [...otherConfigItems, ...s7ConfigItems]

                this.commit('set-config', state.config)
            
            },


            'set-reset-user-input': function(state, resetUserInput) {
                state.resetUserInput = resetUserInput;
            },
            'set-config-loading': function(state, value){
                state.dataConfigLoading = value
            }
        },
        actions: {
            async getConfig({ commit }) {
                const res = await fetch(`${$hostname}/get-config`, { 
                    method: 'POST',  
                    headers: {
                        'Authorization': this.state.token  // Set the Authorization header
                    }
                })
                
                if (res.status === 200) {
                    const config = await res.json();

                    commit('set-config', config); // Commit the config data to the mutation
                }else if(res.status == 403){
                    console.log('navigate to login');
                    router.push({ name: 'login-view'})
                }
            },
            async updateConfig() {
                const res = await fetch(`${$hostname}/get-config`, { 
                    method: 'POST',  
                    headers: {
                        'Authorization': this.state.token  // Set the Authorization header
                    }
                })
                
                if (res.status === 200) {
                    const config = await res.json();

                    return config
                }else if(res.status == 403)
                    router.push({ name: 'login-view'})
            },
        },
        getters: {
            token(state) {
                return state.token;
            },

            getConfig(state) {
                return state.config; // This will now return the object, maintaining reactivity
            },
            getS7InvalidInputs(state) {
                return state.s7InvalidInputs;
            },
            getResetUserInput(state) {
                return state.resetUserInput;
            },
            getConfigLoading(state){
                return state.dataConfigLoading
            }
        }
    });
}

export default store;
