// src/utils/utils.js
function isValidIPv4(ip) {
    // Split the IP string by periods
    const segments = ip.split(".");

    // Check if there are exactly 4 segments
    if (segments.length !== 4) {
        return false;
    }

    // Check each segment to see if it's a valid number between 0 and 255
    for (let segment of segments) {
        // Convert segment to an integer
        const num = parseInt(segment, 10);

        // Check if the segment is a number and within the IPv4 range
        if (isNaN(num) || num < 0 || num > 255 || segment !== num.toString()) {
            return false;
        }
    }

    // If all segments are valid, return true
    return true;
}
module.exports = { isValidIPv4 }
  