import Vue from 'vue'
import App from './App.vue'
import VueRouter from 'vue-router'

Vue.config.productionTip = false

import VueMaterial from 'vue-material'
import 'vue-material/dist/vue-material.min.css'
import 'vue-material/dist/theme/default.css'


Vue.use(VueMaterial)

import VueSweetalert2 from 'vue-sweetalert2';
import 'sweetalert2/dist/sweetalert2.min.css';
Vue.use(VueSweetalert2);

const inProduction = !(process.env.NODE_ENV !== 'production' && process.env.NODE_ENV !== 'test' && typeof console !== 'undefined')
const devServer = 'http://raspberrypi.local:5002';

//const devServer = 'http://172.17.16.199:5002';

Vue.prototype.$hostname = inProduction? '' : devServer;



// Apex Charts
import VueApexCharts from 'vue-apexcharts'
Vue.use(VueApexCharts)
Vue.component('vue-apexchart', VueApexCharts)

import router from '@/router/index.js'
Vue.use(VueRouter)

import createStore from '@/store/index.js'
const STORE = createStore(router);



new Vue({
  router,
  store:STORE,
  render: h => h(App),
}).$mount('#app')
