<template>
    <div class="vertical-wrapper" style="padding: 0 20%;">
        <div class="horizontal-wrapper">
            <md-switch class="md-primary" v-model="dhcp">DHCP enabled</md-switch>
            <div></div>
            <md-button class="md-raised md-primary" v-on:click="saveNetworkConfig">Speichern</md-button>
        </div>

        <div v-if="!dhcp" class="horizontal-wrapper" style="gap: 20pt; ">
            <md-field style="flex: 3 0 0" :class=" { 'md-invalid': !ipValid }">
                <label for="">IP Adresse</label>
                <md-input v-model="ip"></md-input>
                <span class="md-error">Invalid IP</span>

            </md-field>
            <md-field>
                <label for="">Cider</label>
                <md-input v-model="cider"></md-input>
            </md-field>
        </div>
        <div v-if="!dhcp" class="horizontal-wrapper" style="gap: 20pt; ">
            <md-field style="">
                <label for="">Gateway</label>
                <md-input v-model="gateway"></md-input>
            </md-field>
            <md-field :class="{ 'md-invalid': !dnsValid }">
                <label for="">DNS</label>
                <md-input v-model="dns"></md-input>
                <span class="md-error">Invalid DNS</span>
            </md-field>
        </div>
    </div>
</template>

<script>

import { isValidIPv4 } from '../../assets/utils.js'

export default{
    name: 'network-settings',
    beforeRouteEnter(to, from, next){
        next(vm => {
            fetch(`${vm.$hostname}/network/get-config`, { 
                headers:{
                    'Authorization': vm.token  // Set the Authorization header}).then(async (e) => {
                }
            }).then(async (e) => {
                if(e.status != 200)
                    return

                // decode package form server
                const config = await e.json()
                vm.dhcp = config.dhcp;
                vm.ip = config.ip;
                vm.cider = config.cider;
                vm.gateway = config.gateway;
                vm.dns = config.dns;


            })
        })
    },
    methods:{
        async saveNetworkConfig(){
            if((!this.dhcp) && !this.checkSameNetwork(this.ip, this.cider, this.gateway)){
                const { isConfirmed } = this.$swal.fire({
                    icon:'warning',
                    title: `Gateway nicht im Netzwerk`,
                    text: 'Gateway nicht im Netzwerk, dies kann zu Kommunikationsproblemen führen.\nMöchten Sie fortfahren? ',
                    showDenyButton: true,
                    denyButtonText: 'Nein',

                    showConfirmButton: true,
                    confirmButtonText: 'Weiter',
                })
                if(!isConfirmed)    
                    return
            }
           


            const requestBody = {
                dhcp: this.dhcp,
                ip: this.ip,
                cider: this.cider,
                gateway: this.gateway,
                dns: this.dns,
            }

            // 
            fetch(`${this.$hostname}/network/set-config`, {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json',
                    'Authorization': this.token  // Set the Authorization header}).then(async (e) => {

                },
                body: JSON.stringify(requestBody)
            }).then(e => {
                if(e.status != 200){
                    this.$swal.fire({
                        title: 'Fehler',
                        text: 'Bei der Netzwerkeinstellung ist etwas fehlgeschlagen',
                        icon: 'error',
                    });
                }else{
                    this.$swal.fire({
                        title: 'Erfolgreich',
                        text: 'Netzwerkeinstellung gespeichert und bei Neustart aktiv',
                        icon: 'success',
                    });
                }
            });
        },
         // Helper function to convert IP address to a 32-bit integer
        ipToInt(ip) {
            return ip.split('.').reduce((acc, octet) => (acc << 8) + parseInt(octet, 10), 0) >>> 0;
        },
        checkSameNetwork(ip, cidr, gateway) {
           

            // Convert the subnet mask length to a 32-bit integer subnet mask
            const mask = ~((1 << (32 - parseInt(cidr))) - 1) >>> 0;

            // Calculate network addresses for each IP
            const ipNetwork = this.ipToInt(ip) & mask;
            const gatewayNetwork = this.ipToInt(gateway) & mask;

            // Check if IP and Gateway belong to the same network as the base IP
            return ipNetwork === gatewayNetwork;
        },
    },

    computed:{
        ipValid(){
            return isValidIPv4(this.ip)
        },

        dnsValid(){
            return isValidIPv4(this.dns)
        },
        token(){
            return this.$store.getters.token
        }
    },
    data(){
        return{

            dhcp: true,
            ip: '',
            cider: '24',

            gateway: '', 
            dns: ''
        }
    }
}
</script>