<template>
    <div style="overflow: scroll;">
      <md-progress-bar v-if="configLoading" class="md-primary" md-mode="query"></md-progress-bar>
      <div ref="table" style="margin: 10pt "></div>
     
    </div>  
</template>
<script>
import jspreadsheet from 'jspreadsheet-ce'
import '../../../node_modules/jspreadsheet-ce/dist/jspreadsheet.css'
import '../../../node_modules/jsuites/dist/jsuites.css'

import { isValidIPv4 } from '../../assets/utils.js'

var data = [
  [true, '', '', '', '', '', '']
]

const backgroundColorNone = 'background-color: rgba(0, 0, 0, 0);'
const backgroundColorRed = 'background-color: rgba(255, 0, 0, 0.5);'
const backgroundColorYellow = 'background-color: rgba(249, 215, 28, 0.5);'


export default {
  name: 'readS7-view',

  computed:{
    config(){
      return this.$store.getters.getConfig
    },
    resetUserInput(){
      return this.$store.getters.getResetUserInput
    },
    
    configLoading(){
      return this.$store.getters.getConfigLoading
    }
  },

  methods: {
    updateConfigEntire(){

      this.firstUpdate = true;
      // this dispatch loads the entire json config file
      this.$store.dispatch('getConfig').then(() => {


        const s7Data = this.config.data.filter(e => e.type == 's7').map(e => [e.active, e.name, e.unit, e.ip, e.db, e.offset, e.length, e.datatype, e.value? e.value: '', e.readError, false])
        const letters = 'ABCDEFGHIJKLMNOPQRSTUVWXYZ'
        const style = s7Data.reduce((arr, e, i) => {

            if(e[9]){
              // set background of this line to red
              e.forEach((e, index) => arr[letters[index] + (i + 1) ] = backgroundColorRed )

              // add error message to e[8]  
              e[8] = e[9]

            }

            return arr
        }, {})
        if(s7Data.length)
          this.spreadsheet.setData(s7Data)
        else
          this.spreadsheet.setData([Array(10).fill(0).map(() => '')])

        this.spreadsheet.setStyle(style)

        this.firstUpdate = false;

      })

      this.$store.commit('s7-invalid-inputs-reset')

    },
    // this functions ignores user input and just updates the values of the config
    updateConfigValues(){
      // this dispatch loads the entire json config file
      this.$store.dispatch('updateConfig').then((config) => {

        const newData = config.data.filter(e => e.type == 's7').map(e => [e.active, e.name, e.unit, e.ip, e.db, e.offset, e.length, e.datatype, e.value? e.value: '', e.readError])
        const letters = 'ABCDEFGHIJKLMNOPQRSTUVWXYZ'
        
        const style = {}

        const existingData = this.spreadsheet.getData();

        if(this.resetUserInput){
          this.$store.commit('set-reset-user-input', false);
          // override user input
          this.spreadsheet.setColumnData(10, existingData.map(() => false));
        }
        existingData.forEach((row, rowIndex) => {
          const name = row[1]; 
          // Get the name in the first column
    
          // Find the matching row in the new data based on the name
          const newRow = newData.find(newRow => newRow[1] === name);
          
          // If a matching row is found, update columns 8 and 9
          if (newRow && !row[10]) {
            let newValue8 = newRow[8];
            const newValue9 = newRow[9];

            // if newValue9 is not false means error
            if(newValue9 != undefined && newValue9 != null){
              // set background of this line to red
              row.forEach((e, index) => style[letters[index] + (rowIndex + 1) ] = `background-color: rgba(255, 0, 0, 0.5);`)

              // add error message to e[8]  
              newValue8 = newValue9
            }else{
              row.forEach((e, index) => style[letters[index] + (rowIndex + 1) ] = `background-color: white`)
              
            }
            
            

            // Update cells at columns 8 and 9 for the current row
            this.spreadsheet.setValueFromCoords(8, rowIndex, newValue8, true);
            this.spreadsheet.setValueFromCoords(9, rowIndex, newValue9, true);

          }
        });
        
        //this.spreadsheet.setData(s7Data)
        this.spreadsheet.setStyle(style)

        if(this.configLoading && config.data.every(e => e.value || e.readError))
          this.$store.commit('set-config-loading', false)
        

      })

    },
    changeTable(instance, cell, col, row, value){

      const isPasted = cell? false: true

      // if this is a regular update of the values
      if(col == 8 || col == 9 ||  col == 10 || this.firstUpdate)
        return

      let inputValid = true
      
      if(col == 1){  // check if name is empty or duplicate and duplicate is not same value
        inputValid = (value != '') && !this.spreadsheet.getColumnData(1).some((e, i) =>  e == value && i != row)
      }else if(col == 3){ // check if ip is valid IPv4
        inputValid = isValidIPv4(value)
      }else if(col == 4 || col == 6){ // check if length and db are ints
        inputValid = Number.isInteger(Number(value));
      }else if(col == 5){ // check if offset is valued
        const num = Number(value)
        inputValid = !isNaN(num);
      }


      // if value of row is changed (user-input == true) maybe one is all ready a wrong input
      // dont reset styling now
      if(!this.spreadsheet.getValueFromCoords(10, row))
        this.updateStyleRow(row, backgroundColorNone)

      // if the input is not valid change style of the cell
      if(!inputValid){
        this.updateStyleCell(col, row, backgroundColorYellow)
        this.$store.commit('s7-invalid-inputs-add', 1)
      }else{
        this.updateStyleCell(col, row, backgroundColorNone)
        isPasted? 0: this.$store.commit('s7-invalid-inputs-add', -1)

        // save config in store
        
        const table = this.spreadsheet.getData().map(e => { return {active: e[0], type: 's7', name: e[1], unit: e[2], ip: e[3], db: e[4], offset: e[5], length: e[6], datatype:e[7] } })
        
        this.$store.commit('change-s7-config', table)

      }


      // set this row to user-input == true
      this.spreadsheet.setValueFromCoords(10, row, true)



    },

    // 
    saveConfigFile(){

    },

    updateStyleCell(x, y, style){
      if(!this.spreadsheet)
        return

      const styleObject = this.spreadsheet.getStyle()
      const letters = 'ABCDEFGHIJKLMNOPQRSTUVWXYZ'
      const label = letters[x] + (parseInt(y) + 1)

      styleObject[label] = style;

      this.spreadsheet.setStyle(styleObject)

    },
    updateStyleRow(rowIndex, style){
      if(!this.spreadsheet)
        return

      const cols = this.spreadsheet.getWidth()
      for(let i = 0; i < cols.length; i++){
        this.updateStyleCell(i, rowIndex, style)
      }
    },
    onpaste(instance, pasteObject){

      // get the rows from spreadsheet (true) to get row indexes
      const rows = this.spreadsheet.getSelectedRows(true)
      const cols = this.spreadsheet.getSelectedColumns()


      // call the updater function after the paste
      rows.forEach((rowIndex, i) => {
        cols.forEach((colIndex, j) => {
          this.changeTable(instance, null, colIndex, rowIndex, pasteObject[i][j])
        })
      })
     
    }
  },

 
  mounted() {
    // build options
    const options = {
      data: data,
      columns: [
        { type: 'checkbox', title: 'Active', width: '50px' },

        { type: 'text', title: 'Name', width: '200px' },
        { type: 'text', title: 'Unit', width: '120px' },

        { type: 'text', title: 'IP', width: '120px' },
        { type: 'text', title: 'DB', width: '120px' },
        { type: 'text', title: 'offset', width: '120px' },
        { type: 'text', title: 'length', width: '120px' },
        { type: 'dropdown', title: 'DataType', width: '120px', source: ['bit', 'byte', 'word', 'real', 'dint'] },


        { type: 'text', title: 'Value', width: '180px' , readOnly:true },
        { type: 'hidden', title: 'failure'},
        { type: 'hidden', title: 'user-input'},// if is true do not change color
        


      ],
      onchange: this.changeTable,
      onpaste: this.onpaste
    }

    this.updateConfigEntire()


    this.spreadsheet = jspreadsheet(this.$refs['table'], options)
    //Object.assign(this, { spreadsheet })
    const vm = this
    this.requestInterval = setInterval(() => {
     vm.updateConfigValues()
    }, 1000 * 2);
  },
  beforeRouteLeave(to, from, next){
    if(this.requestInterval)
      clearInterval(this.requestInterval)

    next()
  },  
  data(){
    return{
      requestInterval: 0,
      spreadsheet: 0,
      firstUpdate: false,

      loading: true,

     
    }
  }
}


</script>